<template>
  <div class="columns is-centered m-2">
    <div class="column is-half">
      <form v-on:submit.prevent="onSubmit">
        <div class="field">
          <p class="control has-icons-left has-icons-right">
            <input
              class="input"
              type="text"
              placeholder="Name"
              v-model="name"
              maxlength="30"
              minlength="2"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-user"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left has-icons-right">
            <input
              class="input"
              type="email"
              placeholder="Email"
              v-model="email"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
          </p>
          <p v-if="errors.message" class="help is-danger">
            {{ errors.message }}
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="password"
              placeholder="Password"
              v-model="password"
              minlength="6"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
          <p v-if="errors.password" class="help is-danger">
            {{ errors.password }}
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="password"
              placeholder="Confirm Password"
              v-model="confirmPassword"
              minlength="6"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
          <p v-if="errors.password" class="help is-danger">
            {{ errors.password }}
          </p>
        </div>
        <div class="field">
          <p class="control">
            <input
              type="submit"
              name="submit"
              value="Register"
              class="button is-primary"
            />
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
  name: "Register",
  data() {
    return {
      name: "",
      email: "",
      password: "",
      confirmPassword: "",
      errors: {
        password: null,
        message: null,
      },
    };
  },
  methods: {
    ...mapActions(["register"]),

    onSubmit() {
      this.errors.password = null;
      this.errors.message = null;
      if (this.password != this.confirmPassword) {
        this.errors.password = "Passwords do not match";
      } else {
        this.register({
          name: this.name,
          email: this.email,
          password: this.password,
        })
          .then(() => {
            this.$router.replace({
              name: "HomePage",
            });
          })
          .catch((error) => {
            const resCode = error.response.status;
            if (resCode === 401 || resCode === 400) {
              this.errors.message = error.response.data.error;
            } else {
              console.log(error);
            }
          });
      }
    },
  },
};
</script>
