import axios from "axios";

export default {
  state: {
    token: null,
    user: null,
  },
  getters: {
    authenticated(state) {
      return state.token && state.user;
    },

    user(state) {
      return state.user;
    },
  },

  mutations: {
    SET_TOKEN(state, token) {
      state.token = token;
    },
    SET_USER(state, data) {
      state.user = data;
    },
  },

  actions: {
    async login({ dispatch }, credentials) {
      let res = await axios.post(`/auth/login`, credentials);
      return dispatch("attempt", res.data.token);
    },

    async register({ dispatch }, regData) {
      const res = await axios.post(`/auth/register`, regData);
      return dispatch("attempt", res.data.token);
    },

    async attempt({ dispatch, commit, state }, token) {
      if (token) {
        commit("SET_TOKEN", token);
      }
      if (!state.token) {
        return;
      }

      try {
        let res = await axios.get(`/auth/me`);
        commit("SET_USER", res.data.data);
        return dispatch("makeNotebookWithOwner");
      } catch (error) {
        commit("SET_TOKEN", null);
        commit("SET_USER", null);
      }
    },

    async makeNotebookWithOwner({ state }) {
      let savedNotebook = JSON.parse(localStorage.getItem("notebook"));
      if (savedNotebook) {
        if (!savedNotebook.user) {
          let res = await axios.put(`/notebooks/${savedNotebook.id}`, {
            user: state.user._id,
            type: "registered",
          });
          console.log(res);
        }
      }
    },

    logout({ commit }) {
      return axios.get(`/auth/logout`).then(() => {
        commit("SET_TOKEN", null);
        commit("SET_USER", null);
        localStorage.removeItem("notebook");
      });
    },
  },
};
