<template>
  <div class="columns is-centered m-2">
    <div class="column is-half">
      <form v-on:submit.prevent="onSubmit">
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="email"
              placeholder="Email"
              v-model="email"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="password"
              placeholder="Password"
              v-model="password"
              minlength="6"
              required
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
          <p v-if="errors" class="help is-danger">{{ errors.error }}</p>
        </div>
        <div class="field">
          <p class="control">
            <input
              type="submit"
              name="submit"
              value="Login"
              class="button is-primary"
            />
          </p>
        </div>
      </form>
    </div>
