<script>
import { mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      errors: "",
    };
  },
  methods: {
    ...mapActions(["login"]),

    onSubmit() {
      this.login({ email: this.email, password: this.password })
        .then(() => {
          this.$router.replace({
            name: "HomePage",
          });
        })
        .catch((error) => {
          const resCode = error.response.status;
          if (resCode === 401) {
            this.errors = error.response.data;
            this.password = "";
          } else {
            console.log(error);
          }
        });
