import axios from "axios";

class ApiService {
  //create notebook
  static async createNotebook() {
    const res = await axios.post(`/notebooks`);
    const data = res.data.data;
    return data;
  }

  static async getUserNotebooks() {
    const res = await axios.get(`/notebooks/user`);
    const data = res.data;
    return data;
  }

  static async createUserNotebook() {
    const res = await axios.post(`/notebooks/user`);
    const data = res.data.data;
    return data;
  }
  static async getNotebook(notebookId) {
    const res = await axios.get(`/notebooks/name/${notebookId}`);
    const data = res.data.data;
    return data;
  }
  static async getTodosByNotebookId(notebookId) {
    const res = await axios.get(`/notebooks/${notebookId}/todos`);
    const data = res.data;
    return data;
  }
  static async createTodo(todo) {
    const res = await axios.post(`/notebooks/${todo.notebook}/todos`, todo);
    const data = res.data.data;
    return data;
  }
  static async updateNotebookTitle(notebookId, title) {
    const res = await axios.put(`/notebooks/${notebookId}/`, { title });
    const data = res.data.data;
    return data;
  }
  static async updateTodo(todoId, todo) {
    const res = await axios.put(`/todos/${todoId}/`, todo);
    const data = res.data.data;
    return data;
  }
  static async deleteTodo(todoId) {
    const res = await axios.delete(`/todos/${todoId}/`);
    const data = res.data;
    return data;
  }
  static async login(credentials) {
    const res = await axios.post(`/auth/login`, credentials);
    const data = res.data;

    return data;
  }

  static async getUser() {
    const res = await axios.get(`/auth/me`);
    const data = res.data.data;

    return data;
  }
  static async logout() {
    const res = await axios.get(`/auth/logout`);
    const data = res.data;

    return data;
  }

  static async register(regData) {
    const res = await axios.post(`/auth/register`, regData);
    const data = res.data;
    return data;
  }

  static async deleteNotebook(notebookId) {
    const res = await axios.delete(`/notebooks/${notebookId}`);
    const data = res.data;
    return data;
  }
}

export default ApiService;
