<script>
import ApiService from "../ApiService";
import { mapGetters } from "vuex";

export default {
  name: "HomePage",

  data() {
    return {
      isActive: false,
      showNavbar: true,
      error: "",
      notebook: {},
    };
  },
  computed: {
    ...mapGetters(["authenticated", "user"]),
  },
  beforeMount: async function() {
    if (this.authenticated) {
      try {
        const userNotebooks = await ApiService.getUserNotebooks();
        const firstNotebook = userNotebooks.data[0];
        console.log(firstNotebook);
        if (firstNotebook) {
          this.$router.push({
            path: `/${firstNotebook.notebook_name}`,
          });
        }
      } catch (error) {
        console.log(error);
      }
    } else {
      var notebook = JSON.parse(localStorage.getItem("notebook"));

      if (notebook) {
        try {
          //check if notebook still exists
          await ApiService.getNotebook(notebook.notebook_name);
          this.$router.push({
            path: `/${notebook.notebook_name}`,
          });
        } catch (error) {
          if (error.response.status === 404) {
            localStorage.removeItem("notebook");
            this.$router.push({
              path: `/`,
            });
          }
        }
      }
    }
  },
  methods: {
    async createNotebook() {
      if (this.authenticated) {
        try {
          this.notebook = await ApiService.createUserNotebook();
          //set notebook in localstorage
          localStorage.setItem("notebook", JSON.stringify(this.notebook));

          this.$router.push({
            path: `/${this.notebook.notebook_name}`,
          });
        } catch (error) {
          this.error = error.message;
        }
      } else {
        try {
          this.notebook = await ApiService.createNotebook();
          //set notebook in localstorage
          localStorage.setItem("notebook", JSON.stringify(this.notebook));

          this.$router.push({
            path: `/${this.notebook.notebook_name}`,
          });
        } catch (error) {
          this.error = error.message;
        }
      }
    },
  },
};
</script>

