<template>
  <section class="section p-1 m-1">
    <div class="columns">
      <div v-if="authenticated" class="column is-3 list-menu">
        <aside class="menu">
          <p class="menu-label">
            Your Lists
          </p>
          <div>
            <button
              class="button create-list-button mb-5 is-small"
              @click="createUserNotebook"
            >
              <span class="icon">
                <i class="fas fa-plus"></i>
              </span>
              <span>Create New List</span>
            </button>
          </div>

          <ul class="menu-list">
            <li v-for="userNotebook in userNotebooks" :key="userNotebook._id">
              <a
                v-bind:class="{
                  'is-active': isActive(userNotebook.notebook_name),
                }"
                @click.prevent="navToNotebook(userNotebook.notebook_name)"
                >{{ userNotebook.title }}
                <button
                  @click="deleteNotebook(userNotebook._id)"
                  class="delete del-button"
                ></button>
              </a>
            </li>
          </ul>
        </aside>
      </div>
      <div class="column">
        <div class="columns is-vcentered">
          <div class="column is-full box notebook-title">
            <!-- Main container -->
            <nav class="level">
              <!-- Left side -->
              <div class="level-left">
                <div class="level-item">
                  <form class="is-inline" @submit.prevent>
                    <div class="field">
                      <p class="control has-icons-right">
                        <input
                          v-on:keyup.enter="updateTitle"
                          class="input is-large"
                          type="text"
                          minlength="1"
                          maxlength="50"
                          v-model="notebook.title"
                        />
                        <span class="icon is-small is-right">
                          <i class="fas fa-edit"></i>
                        </span>
                      </p>
                    </div>
                  </form>
                </div>
              </div>

              <!--Right side -->
              <div class="level-right">
                <p class="level-item">
                  <a
                    @click.prevent="copyURL"
                    class="button is-small is-primary is-inverted"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {{ this.link_text }}
                  </a>
                </p>
              </div>
            </nav>
          </div>
        </div>
        <div class="columns">
          <div class="column">
            <form @submit.prevent>
              <input
                v-on:keyup.enter="createTodo"
                class="input is-large"
                type="text"
                placeholder="Write your next task here"
                minlength="1"
                maxlength="250"
                v-model="newTodo.text"
              />
            </form>
          </div>
        </div>
        <div class="todo-list" v-if="todos.length > 0">
          <div class="box m-0 todos" v-for="todo in todos" :key="todo._id">
            <div class="columns is-mobile">
              <div class="column is-1">
                <label class="checkbox">
                  <input
                    type="checkbox"
                    :checked="todo.completed"
                    @click="
                      updateTodo(
                        todo._id,
                        { completed: !todo.completed },
                        $event
                      )
                    "
                  />
                </label>
              </div>
              <div class="column">
                <span
                  :id="'span_' + todo._id"
                  @click="showInput(todo._id, $event)"
                  class="todo-span"
                  :class="{ 'todo-is-complete': todo.completed }"
                >
                  {{ todo.text }}
                </span>
                <input
                  :id="'input_' + todo._id"
                  @blur="showSpan(todo._id, $event)"
                  v-on:keyup.enter="
                    updateTodo(todo._id, { text: todo.text }, $event)
                  "
                  :class="{ 'todo-is-complete': todo.completed }"
                  class="input edit-todo"
                  type="text"
                  minlength="1"
                  maxlength="250"
                  v-model="todo.text"
                />
              </div>
              <div class="column">
                <input
                  @change="updateTodo(todo._id, { is_due_date: true }, $event)"
                  v-if="todo.due_date"
                  type="date"
                  name="due-date"
                  :value="new Date(todo.due_date).toISOString().slice(0, 10)"
                  min="1984-01-27"
                />

                <input
                  v-else
                  @change="updateTodo(todo._id, { is_due_date: true }, $event)"
                  placeholder="Date"
                  type="text"
                  readonly="true"
                  onfocus="this.removeAttribute('readonly');this.type='date';
                      this.setAttribute('onfocus','');this.blur();this.focus();"
                />
              </div>
              <div class="column">
                <ul class="task-actions">
                  <li class="m-1">
                    <span
                      v-if="todo.star"
                      class="icon has-text-warning"
                      @click="
                        updateTodo(todo._id, { star: !todo.star }, $event)
                      "
                    >
                      <i class="fas fa-star"></i>
                    </span>
                    <span
                      v-else
                      class="icon"
                      @click="
                        updateTodo(todo._id, { star: !todo.star }, $event)
                      "
                    >
                      <i class="fas fa-star"></i>
                    </span>
                  </li>
                  <li class="m-1">
                    <span
                      class="icon has-text-danger"
                      @click="deleteTodo(todo._id)"
                    >
                      <i class="fas fa-trash"></i>
                    </span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div v-else>
          <h1>You have no tasks!</h1>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import ApiService from "../ApiService";
import { mapGetters } from "vuex";

export default {
  name: "Notebook",
  components: {},
  computed: {
    ...mapGetters(["authenticated"]),
  },

  created: async function() {
    //get notebook data
    try {
      this.notebook = await ApiService.getNotebook(
        this.$route.params.notebookId
      );
      this.newTodo.notebook = this.notebook._id;
      //retrive notebook tasks
      let res = await ApiService.getTodosByNotebookId(this.notebook._id);
      this.todos = res.data;
    } catch (error) {
      this.$router.push({
        path: "/",
      });
    }

    this.populateUserNotebooks();
  },
  updated: function() {},

  data() {
    return {
      errors: "",
      notebook: {},
      todos: [],
      newTodo: {
        text: "",
        notebook: "",
      },
      userNotebooks: [],
      link_url: window.location.href,
      link_text: "Copy Link",
    };
  },
  methods: {
    copyURL() {
      const el = document.createElement("textarea");
      el.value = this.link_url;
      el.setAttribute("readonly", "");
      el.style.position = "absolute";
      el.style.left = "-9999px";
      document.body.appendChild(el);
      const selected =
        document.getSelection().rangeCount > 0
          ? document.getSelection().getRangeAt(0)
          : false;
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      if (selected) {
        document.getSelection().removeAllRanges();
        document.getSelection().addRange(selected);
      }
      this.link_text = "COPIED!";
    },
    async populateUserNotebooks() {
      //populate user notebooks if authenticated
      if (this.authenticated) {
        try {
          const userNotebooks = await ApiService.getUserNotebooks();
          this.userNotebooks = userNotebooks.data;
        } catch (error) {
          console.log(error);
        }
      }
    },
    async createUserNotebook() {
      if (this.authenticated) {
        try {
          const res = await ApiService.createUserNotebook();
          const notebookName = res.notebook_name;
          this.$router.push({
            path: "/" + notebookName,
          });
        } catch (error) {
          console.log(error);
        }
      }
    },
    async createTodo() {
      //Create new todo
      if (this.newTodo.text.length > 0 && this.newTodo.text.length <= 250) {
        try {
          await ApiService.createTodo(this.newTodo);
          this.newTodo.text = "";
          //event.target.blur();
          //fetch all todos again
          let res = await ApiService.getTodosByNotebookId(this.notebook._id);
          this.todos = res.data;
        } catch (error) {
          console.log(error);
        }
      }
    },
    async updateTitle(event) {
      if (this.notebook.title.length > 0 && this.notebook.title.length <= 50) {
        try {
          let res = await ApiService.updateNotebookTitle(
            this.notebook._id,
            this.notebook.title
          );
          this.notebook = res;
          event.target.blur();
          if (this.authenticated) {
            const userNotebooks = await ApiService.getUserNotebooks();
            this.userNotebooks = userNotebooks.data;
          }
        } catch (error) {
          console.log(error);
        }
      }
    },
    async updateTodo(todoId, data, event) {
      try {
        if (data.is_due_date) {
          data.due_date = event.target.value;
        }

        await ApiService.updateTodo(todoId, data);
        //fetch all todos again
        let res = await ApiService.getTodosByNotebookId(this.notebook._id);
        this.todos = res.data;
        event.target.blur();
        var input = "input_" + todoId;
        var inputEle = document.getElementById(input);
        inputEle.style.display = "none";

        var span = "span_" + todoId;
        var spanEle = document.getElementById(span);
        spanEle.style.display = "inline-block";

        inputEle.focus();
      } catch (error) {
        console.log(error);
        let res = await ApiService.getTodosByNotebookId(this.notebook._id);
        this.todos = res.data;
        event.target.blur();
      }
    },
    async deleteTodo(todoId) {
      try {
        await ApiService.deleteTodo(todoId);
        //fetch all todos again
        let res = await ApiService.getTodosByNotebookId(this.notebook._id);
        this.todos = res.data;
      } catch (error) {
        console.log(error);
      }
    },
    showInput(todoId, event) {
      event.target.style.display = "none";
      var input = "input_" + todoId;
      var inputEle = document.getElementById(input);
      inputEle.style.display = "inline-block";
      inputEle.focus();
    },
    showSpan(todoId, event) {
      event.target.style.display = "none";

      var span = "span_" + todoId;
      var spanEle = document.getElementById(span);
      spanEle.style.display = "inline-block";
    },
    toDate(event) {
      event.target.type = "date";
    },
    toText(event) {
      event.target.type = "text";
    },
    navToNotebook(notebookName) {
      this.$router.push({
        path: `/${notebookName}`,
      });
    },
    async deleteNotebook(notebookId) {
      try {
        await ApiService.deleteNotebook(notebookId);
        await this.populateUserNotebooks();
        this.$router.push({
          path: `/${this.userNotebooks[0].notebook_name}`,
        });
      } catch (error) {
        console.log(error);
      }
    },
    isActive(name) {
      if (name === this.$route.params.notebookId) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

